.about-us{
    display: flex;
    justify-content: space-between;
    align-items:center;
    padding: 57px 105px 135px 120px;
    .about-info-wrapper{
        min-height: 247px;
        width: 35%;
        .about-descr{
            margin-top: 36px;
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
            color: #fff;
        }
    }
}

@media (max-width: 1200px) {
    .about-us{
        .about-info-wrapper{
            width: 50%;
        }
        .about-us-pic{
            width: 400px
        }
    }
}

@media (max-width: 992px) {
    .about-us{
        flex-direction: column;
        .about-info-wrapper{
            width: 100%;
        }
        .about-us-pic{
            width: 70%;
            margin-top: 50px;
        }
    }
}

@media (max-width: 768px) {
    .about-us{
        margin-top: 70px;
        .about-info-wrapper{
            .about-descr{
                text-align: center;
                font-size: 18px;
             }
        }
        .about-us-pic{
            width: 80%;
        }
        padding: 57px 20px 135px 20px;
    }
}

@media (max-width: 576px) {
    .about-us{
        .about-us-pic{
            width: 100%;
        }
        padding: 57px 20px 80px 20px;
    }
}