.fees{
    position: relative;
    min-height: 360px;
    padding-top: 78px;
    .union-circles-pic{
        position: absolute;
        left: 0px;
        bottom: -120px;
        z-index: -1;
    }
    .fees-card{
        margin: 0 auto;
        display: block;
        border-radius: 20px;
        max-width: 1200px;
        height: 236px;
        background-color: #111;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 66px 117px;
        .percentage{
            font-weight: 800;
            font-size: 64px;
            line-height: 100%;
            color: #3a86ff;
        }
        .percentage-name{
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
            color: #fff;
            margin-top: 16px;
        }
    }
}

@media (max-width: 992px) {
    .fees{
        .fees-card{
            padding: 30px 30px;
        }
    }
}

@media (max-width: 768px) {
    .fees{
        svg{
            rotate: 90deg;
        }
        .fees-card{
            display: block;
            height: 100%;
            width: 50%;
            text-align: center;
        }
        .union-circles-pic{
            width: 100%;
            height: auto;
        }
    }
}

@media (max-width: 414px) {
    .fees{
        .fees-card{
            width: 80%;
        }
    }
}